import React, { Component } from 'react';
import '../styles/App.css';
import '../styles/animate.css';
import $ from 'jquery';
import { BrowserRouter as Router, Route, Link } from 'react-router-dom';

//<input id="topic-field" type="text" placeholder="Enter a Topic"></input>
window.synops = []
class VisualizeBlock extends Component {
  constructor(props){
    super(props)
    var topic = props.match.params.topic;

    var pages = window.titles.titles;
    if(window.titles.titles != undefined){
    window.xhr = $.ajax({
      type:'GET',
      url:'https://cors-anywhere.herokuapp.com/https://wikisumapi.herokuapp.com/wiki/' + window.titles.titles.join('+') +'/summaries',
      dataType:'json',
      success: (data) =>{
        window.summaries = data;
        window.synops = window.summaries.summaries
        this.setState({titles:data});
        for(var i = 0;i<window.synops.length;i++){
          $("#desc"+i).text(window.synops[i])
        }
      },
      error: (data)=>{

        console.log(data)
      }
    })}
    console.log(window.titles)
    this.state = {topic:topic,titles:pages, descriptions:['Lorem ipsum dolor veritas et sas','fuckeasdfasdfasdfadfasdf','aasdsdfdsfasdadsfadsfvadsf','asdfuuerajerjweqwer']}

  }
  backHome(){

    this.props.history.push('/');

  }

  topicEnter(){

    

  }
  componentOnMount(){
    //$('#topic-field').addClass('fadeInUp animated');
  }
  updateQuery(e){
        console.log(e.target.innerText)
        window.xhr.abort().then(()=>{
          $.ajax({
            type:'GET',
            url:'https://crossorigin.me/https://wikisumapi.herokuapp.com/wiki/' + window.titles.titles.join('+') +'/summaries',
            dataType:'json',
            success: (data) =>{
              window.titles = data;
              this.setState({titles:data});
              this.props.history.push('/visualize/'+ topic.replace(/ /g,'_'))
            },
            error: (data)=>{
    
              console.log(data)
            }})
          });
        
        var topic = e.target.innerText
        $('#topic-field').val(e.target.innerText)    
        
        
  }
  onChange(e){
    
  }
  render() {
    if(window.titles != 0){ 
    return (
        <div id="vis-space" className='container'>     
                    
            {window.titles.titles.map((elem, i) => {
              return(
                <div className="padOverride col-s-6 col-xs-12">   
              <div key={elem} className="page-box">
                <Link value = {elem} onClick = {this.updateQuery.bind(this)} to={'/visualize/'+elem}><h1>{elem.replace(/_/g,' ')}</h1></Link><h2 id={"desc"+i}>{}</h2>
              </div>
              </div>)

            })}
            
      </div>
    );
}else{return false}}}

export default VisualizeBlock;



