import React, { Component } from 'react';
import './styles/App.css';
import './styles/animate.css';
import Home from './components/Home'
import Visualize from './components/Visualize'
import VisualizeBlock from './components/VisualizeBlock'
import $ from 'jquery';
import {
  BrowserRouter as Router,
  Route,
  Redirect,
  Link
} from 'react-router-dom'
//<input id="topic-field" type="text" placeholder="Enter a Topic"></input>

class App extends Component {
  constructor(props){
    super(props)

  }
  render(props) {
    return (
    <Router>
      <div className="Home">
        <Route exact path="/" component={Home}/>
        <Route path="/visualize/" component={Visualize}></Route>
        <Route exact path="/visualize/:topic" component = {VisualizeBlock}/>  
      </div>
    </Router>
    );
  }
}

export default App;



