import React, { Component } from 'react';
import '../styles/App.css';
import '../styles/animate.css';
import {observer} from 'mobx-react'
import $ from 'jquery';
import { BrowserRouter as Router, Route, Link } from 'react-router-dom';

//<input id="topic-field" type="text" placeholder="Enter a Topic"></input>
window.titles = []
@observer class Visualize extends Component {
  constructor(props){
    super(props)
    var topic = props.match.params.topic;
    
    this.state = {topic:topic,titles:['Distributed Network', 'Cryptocurrency','Encryption','Cryptography'], descriptions:['Lorem ipsum dolor veritas et sas','fuckeasdfasdfasdfadfasdf','aasdsdfdsfasdadsfadsfvadsf','asdfuuerajerjweqwer']}
    if(this.state.topic===undefined){
      this.state.topic = '';
       
    }

  }
  handleKeyPress(event){
    if(event.key == 'Enter'){      
      this.setState({topic:event.target.value})
      window.topic = event.target.value;
      var topic = window.topic
      $.ajax({
        type:'GET',
        url:'https://cors-anywhere.herokuapp.com/https://wikisumapi.herokuapp.com/wiki/' + topic.replace(/ /g,'_') +'/titles',
        dataType:'json',
        success: (data) =>{
          window.titles = data;
          this.setState({titles:data});
          this.props.history.push('/visualize/'+ topic.replace(/ /g,'_'))
        },
        error: (data)=>{

          console.log(data)
        }
      })

      
    }
  }

  backHome(){

    this.props.history.push('/');

  }

  topicEnter(){

    

  }
  componentWillMount(){
    if(window.topic === undefined){
      this.props.history.push('/visualize')

    }


  }
  componentDidMount(){
         

    //$('#topic-field').addClass('fadeInUp animated');
  }
  onChange(e){

    this.setState({topic:e.target.value})
  }
  render(props) {
    return (
    <div>
      <div id="top-bar">
        <h1 id="top-badge" onClick={this.backHome.bind(this)} className="page-title">WikiSum</h1>
        <input ref="queryfield" onKeyPress = {this.handleKeyPress.bind(this)} id="topic-field" onChange={this.onChange.bind(this)} value={this.state.topic.replace(/_/g, ' ')} type="text" placeholder="Enter Topic"/>
      </div>
    </div>
    );
  }
}

export default Visualize;



