import React, { Component } from 'react';
import '../styles/App.css';
import '../styles/animate.css';
import $ from 'jquery';
import Particles from 'particlesjs'
import { BrowserRouter as Router, Route, Link } from 'react-router-dom';

//<input id="topic-field" type="text" placeholder="Enter a Topic"></input>

class Home extends Component {

  componentDidMount(){

    $('.page-title').addClass('fadeInUp animated').delay(510).queue(function(next){

      $('#page-subtitle').addClass('fadeInUp animated').delay(510).queue(function(next){
        
        $('#enter').addClass('fadeInUp animated'); 
        next();
      }); ; 
      next();
    }); 
    Particles.init({
      maxParticles:40,
      selector:'.pc',
      minDistance:400,
      color:"#12D8FA",
      connectParticles:true

    })
    //$('#topic-field').addClass('fadeInUp animated');
  }
  render() {
    return (
      <div className="App">
        <canvas width="100%" height = "15vh" className="pc"></canvas>
        <h1 className="page-title">Wikisum</h1>
        <h3 id="page-subtitle" className="">A Wikipedia Knowledge Representation Utility</h3>
        <Link to='/visualize/'><button id="enter" className="">Enter</button></Link>
      </div>
    );
  }
}

export default Home;



